.header {
	border-top: 15px solid #272727;
	padding: 10px 50px;
	display: flex;
	flex-direction: row;
	align-items: center;
}

.header > img {
	border-radius: 50%;
	width: 250px;
}

.header-wrapper {
	margin-left: 40px;
}

.header-tile {
	font-size: 64px;
	margin: 0;
}

.header-subtitle {
	margin: 0;
	max-width: 300px;
}

@media screen and (max-width: 650px) {
	.header > img {
		width: 150px;
	}
	.header-tile {
		font-size: 42px;
	}
}

@media screen and (max-width: 480px) {
.header {
	flex-direction: column;
}
}
